














body {
    background: #e7e7e7;
    margin: 0;
    padding: 0;
}
#header p,
    #header h4  {
        margin: 0;
        display: inline-block;
}
#user-info p {
        font-size: 12px;
}
#user-info {
        display: inline-block;
        line-height: 35px;
}
#last-trade {
        float: right;
        margin-right: 40px;
        line-height: 70px;
        display: inline-block;
}
#last-trade h4 {
        margin: 0;
}
#header {
        background: #c8c8c8;
        width: 100%;
        padding: 0 20px;
        height: 70px;
        border-bottom: 1px #999 solid;
}
#birj-body-main {
        padding: 10px;
}
#birj-body-inner {
        width: 100%;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
}
.col {
        margin: 0 10px;
        width: 50%;
        background: #fff;
        border-radius: 6px;
}
#buy-form {
        height: 100px;
        line-height: 25px;
        padding: 10px;
}
#price {display: inline-block;
}
form p {
        width: 100px;
        display: inline-block;
        margin: 0;
}
#last-trades ul {
        margin: 10px 0;
        padding: 0 10px;
        max-height: 500px;
        overflow-y: scroll;
}
#last-trades ul li {
        position: relative;
        background: #e8e8e8;
        border: 1px #999 solid;
        margin: 5px 0;
        list-style: none;
        padding: 5px 5px 17px;
}
#last-trades ul li i {
        float: right;
        font-size: 12px;
        font-weight: bold;
}
#last-trades ul li button {
        position: absolute;
        top: 5px;
        right: 5px;
        border: none;
        background: transparent;
        font-weight: bold;
        color: #999;
}
#last-trades ul li button:hover {
        color: #666;
}
